.popup__button {
  width: 100%;
  height: 50px;
  border: 1px solid rgba(0, 0, 0, 0.2);
  margin-top: 48px;
  color: rgba(0, 0, 0, 0.2);
  font-size: 36px;
  cursor: default;
}

.popup__button_edit,
.popup__button_ava {
  font-size: 18px;
}

.popup__button_active {
  background-color: #000000;
  color: #ffffff;
  cursor: pointer;
}
